 <template>
    <div class="root">
        <v-card text>
       <v-row >
      <v-col cols="12">
             <h2 class="h2">  </h2>     
      </v-col>
      </v-row> 

                     <v-toolbar text prominent class="no-padding-toolbar">
                <!--   "", "",
                "" -->         
                <v-toolbar-items>
                    <v-tooltip bottom>
                       <template v-slot:activator="{ on }"  >
                       <v-btn v-on="on" @click="dialog = true,edit=false">
                            <v-icon medium>add</v-icon>
                        </v-btn>
                        </template>
                        <span>   </span>
                    </v-tooltip>
                      <v-tooltip bottom>
                        <template v-slot:activator="{ on }"  >
                          <v-btn v-on="on" @click="editItem">
                            <v-icon medium>edit</v-icon>
                          </v-btn>
                        </template>
                        <span></span>
                    </v-tooltip>
                    <v-tooltip bottom>
                       <template v-slot:activator="{ on }"> 
                        <v-btn v-on="on"  @click="delItem" >
                           <v-icon medium>delete</v-icon>
                        </v-btn>
                        </template>
                        <span> </span>
                    </v-tooltip>
                </v-toolbar-items>
                <!--   -->
                <v-text-field
                        v-model="search"
                        append-icon="search"
                        :label="search"
                        single-line
                        hide-details>
                </v-text-field>
            </v-toolbar>
            <v-divider>
            </v-divider>
           
           <!--  :tran1=""-->
           <!--
           
            

            https://vuetifyjs.com/en/components/data-tables/             


            multi-sort-     
                  -

            show-select-   
            :single-select="singleSelect" -   
             
             data:  
            singleSelect:true,

            v-model="search"   label="Search"  
            :headers="commonHeaders" -     commonHeaders

            :items="portals" -     portals

            class="fixed-headers" -  

            :items-per-page="5" -      
           -->   
            
            <!--     (commonHeaders)
               (  portals  )-->
            <v-data-table
                    :headers="commonHeaders"
                    :items="portals"
                    :search="search"
                    v-model="selected"
                    item-key="id"
                    :no-data-text="' '"
                    :no-results-text="'  '"
                    show-select
                    :single-select="singleSelect"
                    class="fixed-headers"
                    :items-per-page="5"
                    >

            </v-data-table>
    <v-flex text-sm-center align-center>
                 <v-btn
                 class="btn1"
                 color= "#E1F5FE"
                 @click="returnBack()"
                  >
                    
                  </v-btn>
      </v-flex>            
         

        </v-card>


        <!--
                              Dilog    
             "  "
         " " -->
        <v-dialog v-model="dialog" persistent max-width="600px">
            <v-card>
                <v-form ref="form">
                    <v-card-text>
                        <v-container grid-list-md>
                            <v-layout wrap>                    
                               <v-flex xs12>
                                    <v-text-field :label="' '" v-model="portalId"                                    
                                                  required></v-text-field>
                                </v-flex>

                                <v-flex xs12>                                               
                               <template>
                                 <p>  : </p>                               
                               </template>
                               
                             <v-combobox
                                   v-model="selectedType"
                                   :items="integrationTypes"
                                   item-text="name"
                                   item-value="name"
                                   label=" "
                                   dense
                                   filled
                                   outlined
                                   solo
                                ></v-combobox> 
          
                                </v-flex>


                            </v-layout>
                        </v-container>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="close" closeMethod>{{''}}</v-btn>
                        <v-btn color="blue darken-1" text @click="addEditItem">{{''}}</v-btn>
                    </v-card-actions>
                </v-form>
            </v-card>
        </v-dialog>
        <!-- ********************************************************  -->
    </div>
</template>

<script>

export default {
      
    data() {
        return {
                pageName:'',                
                id:'',
                portalId:'',
                integrationType:{id:"",name:"",code:""},        
                dialog: false,
                edit:false,
                selected: [],
                search: '',
                singleSelect:true,
                commonHeaders: [
                    {text: 'id', align: 'left', value: 'id'},
                    {text: ' ', align: 'left', value: 'portalId'},
                    {text: ' ', align: 'left', value: 'integrationType.name'},

                ],
                selectedType: {id:"",
                name:" ", 
                code:""}


            }
        },
 methods: { 
            returnBack(){
             this.pageName = this.$store.getters.pageName

            if (this.pageName==="employee") {
            this.$router.push('/employee')        }
         
            else if (this.pageName==="project") {
            this.$router.push('/project')       
            }
            },

            //      " "
            //     Dilog
            // ( )
               editItem() {
                  if (this.selected.length === 0) {
                    alert('    ')
                } else {
                console.log(this.selected[0]);
                //    
                 this.id=this.selected[0].id;
                 this.portalId=this.selected[0].portalId;      
                //  :             
                 // this.integrationType = this.selected[0].integrationType
                 //   ,  :
                 let a =   this.selected[0] 
                 let b={}                  
                 b.id =      a.integrationType.id
                 b.name =    a.integrationType.name 
                 b.code =    a.integrationType.code           
                 
                this.integrationType = b 

                 //console.log('a.integrationType.id',a.integrationType.id)  
                // console.log('b.id',b.id) 
                // console.log('this.integrationType ',this.integrationType ) 
                 
                //  -    
                //      
                // (       ,
                //    )
                //  :
                // this.selectedType =  this.selected[0].integrationType
                //   ,  
                this.selectedType = b;
                console.log('this.selectedType ',this.selectedType ) 

                 this.edit=true
                 this.dialog = true                 
                }
            },   
            //     ""
            //    "Dilog"
            addEditItem() {
                //   
                this.dialog = false;
                //       
                //      id    
                if (this.edit === false) {
                let form = {
                    id: this.id,
                    portalId: this.portalId,
                    integrationType: this.selectedType
                };

                    this.$store.dispatch('addPortal', form);
                    this.$refs.form.reset()
                                      }
                //      
                //    
                 if (this.edit === true) {


                let form = {
                    id: this.id,
                    portalId: this.portalId,
                    integrationType: this.selectedType
                };

                    const msg = '   ?';
                    let boo = confirm(msg) && this.$store.dispatch('editPortal', form);
                    if (boo === true) {
                        this.selected = [];
                    } else {
                        this.selected = [];
                    }

                    
                    this.$refs.form.reset()
                                      }
            },
          //      
          //   Dilog
            close() {
                this.dialog = !this.dialog;
                this.$refs.form.reset();
                this.isEdit = false;
            },

             //   
            delItem() {
                if (this.selected.length === 0) {
                    alert('    ')
                } else {
                    const msg = ' ?';
                    let boo = confirm(msg) && this.$store.dispatch('deletePortal', this.selected[0])
                    if (boo === true) {
                        this.selected = [];
                    } else {
                        this.selected = [];
                    }
                }
            },
    
            setData() {
           //     this.$store.dispatch('portals')
            }
        },


        created() {
           //  this.setData();
        },

        watch: {
       
        },

        computed: {           
        //     portals  store/portals
            portals() {
                    return this.$store.getters.portals
            },
        //      integrationTypes
            integrationTypes() {
              return this.$store.getters.integrationTypes
            }
        }
    }

</script>

